.card { 
  border: 1px solid goldenrod;
  /*background-color: rgba(0,0,0,0.5);*/
   background-image: url("https://2img.net/h/i21.photobucket.com/albums/b287/N00l3/bg.jpg"); 
    margin: .8rem;
    position: relative;
    text-align: left;
    border-style: none;
  }
  
  .card > .img-container {
    overflow: hidden;
    text-align: center;
  }
  .card > .img-container > img {
    height: 100px; 
    width: 100px; 
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  .click-item{ 
    border-radius: 25px;
  }
.wrapper {
  background-image: url("https://2img.net/h/i21.photobucket.com/albums/b287/N00l3/bg.jpg");
    padding-top: 50px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
.resultModal {
    position: fixed;
    top: 0;
    left: 0;
    left: 0;
    z-index: 1050;
    overflow: hidden;
    outline: 0;
    height: 100%;
    width: 100%; 
    background-color: rgba(0,0,0,0.5);
    display:flex; 
    align-items:center; 
    justify-content: center;   
}

#modalContent {
    background-image: url("https://backgroundcheckall.com/wp-content/uploads/2018/10/runescape-website-background-1-300x200.jpg");
    /*background-color: #fff;*/ 
    border-radius: 25px;
    height: 400px;
    width: 600px; 
    display:flex; 
    align-items:center; 
    justify-content: center; 
    flex-direction: column;  
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  margin: 0;
